/* Virtual Storage Pool */
	const Pool = {};

/* Handle Error Event */
	function OnError_Key(Method, Key) {
		console.error("%cSimplify JavaScript - Virtual Storage", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string!");
	};
	function OnError_KeyArray(Method, Key) {
		console.error("%cSimplify JavaScript - Virtual Storage", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Pressed \"undefined\" as part of the result.");
	};

/* Set */
	function Set(Key, Value = null) {
		if (typeof Key == "string") {
			Pool[Key] = Value;
		} else {
			OnError_Key("Set", Key);
		};
	};

/* Get */
	function Get_Internal(Key) {
		return Pool[Key];
	};
	function Get(Key = null) {
		if (typeof Key == "string") {
			return Get_Internal(Key);
		} else if (Array.isArray(Key) == true) {
			let ValueArray = [];
			for (let index = 0; index < Key.length; index++) {
				if (typeof Key[index] == "string") {
					ValueArray.push(Get_Internal(Key[index]));
				} else {
					OnError_KeyArray("Get", Key[index]);
					ValueArray.push(undefined);
				};
			};
			return ValueArray;
		} else if (Key == null) {
			return Pool;
		} else {
			OnError_Key("Get", Key);
			return undefined;
		};
	};

/* Delete */
	function Delete_Internal(Key) {
		delete Pool[Key];
	};
	function Delete(Key) {
		if (typeof Key == "string") {
			Delete_Internal(Key);
		} else if (Array.isArray(Key) == true) {
			for (let index = 0; index < Key.length; index++) {
				if (typeof Key[index] == "string") {
					Delete_Internal(Key[index]);
				} else {
					OnError_KeyArray("Delete", Key[index]);
				};
			};
		} else {
			OnError_Key("Delete", Key);
		};
	};
	function Del(Key) {
		return Delete(Key);
	};
	function DeleteAll() {
		let KeyArray = Object.keys(Pool);
		for (let index = 0; index < KeyArray.length; index++) {
			Delete_Internal(KeyArray[index]);
		};
	};
	function DelAll() {
		return DeleteAll();
	};

/* Package Module */
	export {
		Set,
		Get,
		Delete,
		Del,
		DeleteAll,
		DelAll
	};
