/* Root */
	Math.root = function(Input = 1, Root = 1) {
		if (typeof Input == "number" && typeof Root == "number") {
			return Math.pow(Input, (1/Root));
		} else {
			if (typeof Input != "number") {
				console.error("Math.root: Input is not a number!");
			};
			if (typeof Root != "number") {
				console.error("Math.root: Root is not a number!");
			};
			return undefined;
		};
	};

/* Greatest Common Divisor */
	Math.gcd = function() {
		let Chain = [];
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
		} else {
			for (let index = 0; index < arguments.length; index++) {
				Chain.push(arguments[index]);
			};
		};
		if (Chain.length >= 1) {
			for (let index = 0; index < Chain.length; index++) {
				if (typeof Chain[index] != "number") {
					console.error("Math.gcd: Not a number!");
					return undefined;
				};
			};
		} else {
			console.error("Math.gcd: No input!");
			return undefined;
		};
		if (Chain.length == 2) {
			if (Chain[1] == 0) {
				return Chain[0];
			} else {
				return Math.gcd(Chain[1], Chain[0] % Chain[1]);
			};
		} else if (Chain.length > 2) {
			var result = Math.gcd(Chain[0], Chain[1]);
			for (let index = 2; index < Chain.length; index++) {
				result = Math.gcd(result, Chain[index]);
			};
			return result;
		} else {
			return Chain[0];
		};
	};
	Math.gcf = Math.gcd; // Greatest Common Factor
	Math.hcf = Math.gcd; // Highest Common Factor
	Math.gcm = Math.gcd; // Greatest Common Measure
	Math.hcd = Math.gcd; // Highest Common Divisor

/* Least Common Multiple */ // Lowest Common Multiple
	Math.lcm = function() {
		let Chain = [];
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
		} else {
			for (let index = 0; index < arguments.length; index++) {
				Chain.push(arguments[index]);
			};
		};
		if (Chain.length >= 1) {
			for (let index = 0; index < Chain.length; index++) {
				if (typeof Chain[index] != "number") {
					console.error("Math.lcm: Not a number!");
					return undefined;
				};
			};
		} else {
			console.error("Math.lcm: No input!");
			return undefined;
		};
		if (Chain.length >= 2) {
			let Top = 1;
			for (let index = 0; index < Chain.length; index++) {
				Top *= Chain[index];
			};
			let Bottom = Math.gcd(Chain);
			return (Math.abs(Top) / Bottom);
		} else {
			return Chain[0];
		};
	};
	Math.scm = Math.lcm; // Smallest Common Multiple

/* Hypotenuse */
	Math.hypotenuse = function(A, B, Query = "h") {
		if (typeof A == "number" && typeof B == "number") {
			switch (Query) {
				case "h": case "H":
					return Math.sqrt((A * A) + (B * B));
					break;
				case "c": case "C":
					return Math.sqrt((A * A) - (B * B));
					break;
				default:
					console.error("Math.hypotenuse: Invalid query!");
					return undefined;
					break;
			};
		} else {
			console.error("Math.hypotenuse: Not a number!");
			return undefined;
		};
	};
	Math.antihypotenuse = function(A, B) {
		return Math.hypotenuse(A, B, "c");
	};
	Math.xhypotenuse = Math.antihypotenuse;

/* Factorial */
	Math.factorial = function(n) {
		if (Number.isInteger(n) == true && n > 0) {
			let Total = n;
			while (n > 1) {
				n--;
				Total = Total * n;
			};
			return Total;
		} else if (Number.isInteger(n) == true && n == 0) {
			return 1;
		} else {
			console.error("Math.factorial: Not a number, integer, or positive number!");
			return undefined;
		};
	};
	Math.factor = Math.factorial;
	Math.fact = Math.factorial;

/* Permutation */
	Math.permutation = function(n, k) {
		if (Number.isInteger(n) == true && Number.isInteger(k) == true && n >= 1 && k >= 1 && n >= k) {
			return (Math.factorial(n) / Math.factorial(n - k));
		} else {
			if (Number.isInteger(n) != true || Number.isInteger(k) != true || n < 1 || k < 1) {
				console.error("Math.permutation: Not a number, integer, or positive number!");
			};
			if (n < k) {
				console.error("Math.permutation: Illogical permutation!")
			};
			return undefined;
		};
	};
	Math.nPr = Math.permutation;
	Math.permut = Math.permutation;
	Math.cumulativepermutation = function(n) {
		if (Number.isInteger(n) == true && n >= 1) {
			let Total = 0;
			for (let index = 1; index <= n; index++) {
				Total = Total + Math.permutation(n, index);
			};
			return Total;
		} else {
			console.error("Math.cumulativepermutation: Not a number, integer, or positive number!");
			return undefined;
		};
	};
	Math.cumpermut = Math.cumulativepermutation;

/* Combination */
	Math.combination = function(n, k) {
		if (Number.isInteger(n) == true && Number.isInteger(k) == true && n >= 1 && k >= 1 && n >= k) {
			return (Math.factorial(n) / (Math.factorial(k) * Math.factorial(n - k)));
		} else {
			if (Number.isInteger(n) != true || Number.isInteger(k) != true || n < 1 || k < 1) {
				console.error("Math.combination: Not a number, integer, or positive number!");
			};
			if (n < k) {
				console.error("Math.combination: Illogical combination!")
			};
			return undefined;
		};
	};
	Math.nCr = Math.combination;
	Math.combin = Math.combination;
	Math.cumulativecombination = function(n) {
		if (Number.isInteger(n) == true && n >= 1) {
			let Total = 0;
			for (let index = 1; index <= n; index++) {
				Total = Total + Math.combination(n, index);
			};
			return Total;
		} else {
			console.error("Math.cumulativecombination: Not a number, integer, or positive number!");
			return undefined;
		};
	};
	Math.cumcombin = Math.cumulativecombination;

/* Sort */
	Math.sort = function() {
		let Chain = [];
		let Method;
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
			Method = arguments[1];
		} else {
			for (let index = 0; index < (arguments.length - 1); index++) {
				Chain.push(arguments[index]);
			};
			if (typeof arguments[arguments.length - 1] == "number") {
				Chain.push(arguments[arguments.length - 1]);
			} else {
				Method = arguments[arguments.length - 1];
			};
		};
		if (Chain.length >= 1) {
			for (let index = 0; index < Chain.length; index++) {
				if (typeof Chain[index] != "number") {
					console.error("Math.sort: Not a number!");
					return undefined;
				};
			};
		} else {
			console.error("Math.sort: No input!");
			return undefined;
		};
		let Queue = [];
		switch (Method) {
			case "Ascending": case "ascending": case "LH": case "AZ": case "": case "Normal": case "normal": case undefined:
				while (Chain.length > 1) {
					let X = Math.min(...Chain);
					Queue.push(X);
					let Position = Chain.indexOf(X);
					Chain.splice(Position, 1);
				};
				Queue.push(Chain[0]);
				return Queue;
				break;
			case "Descending": case "descending": case "HL": case "ZA": case "Reverse": case "reverse":
				while (Chain.length > 1) {
					let X = Math.max(...Chain);
					Queue.push(X);
					let Position = Chain.indexOf(X);
					Chain.splice(Position, 1);
				};
				Queue.push(Chain[0]);
				return Queue;
				break;
			default:
				console.error("Math.sort: Invalid sort method!");
				return undefined;
				break;
		};
	};

/* Mean */
	Math.mean = function() {
		let Chain = [];
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
		} else {
			for (let index = 0; index < arguments.length; index++) {
				Chain.push(arguments[index]);
			};
		};
		if (Chain.length >= 1) {
			for (let index = 0; index < Chain.length; index++) {
				if (typeof Chain[index] != "number") {
					console.error("Math.mean: Not a number!");
					return undefined;
				};
			};
		} else {
			console.error("Math.mean: No input!");
			return undefined;
		};
		return (Math.addition(Chain) / Chain.length);
	};
	Math.average = Math.mean;

/* Median */
	Math.median = function() {
		let Chain = [];
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
		} else {
			for (let index = 0; index < arguments.length; index++) {
				Chain.push(arguments[index]);
			};
		};
		if (Chain.length >= 1) {
			for (let index = 0; index < Chain.length; index++) {
				if (typeof Chain[index] != "number") {
					console.error("Math.median: Not a number!");
					return undefined;
				};
			};
		} else {
			console.error("Math.median: No input!");
			return undefined;
		};
		Chain = Math.sort(Chain, "Ascending");
		switch (Chain.length % 2) {
			case 0:
				return Math.mean(Chain[Chain.length / 2 - 1], Chain[Chain.length / 2]);
				break;
			case 1:
				return Chain[Chain.length / 2 - 0.5];
				break;
		};
	};
