/* Library Data */
	const LibraryVersion = "0.1.0";

/* Math */
	/* Extension */
		import "./math/extension.js";
	/* Prototype */
		import "./math/prototype.js";

/* Library Core */
	/* Version */
		function Version(Semantic) {
			let LibraryVersion_Split = LibraryVersion.split(".");
			if (Semantic == undefined) {
				return LibraryVersion;
			} else if (Number.isInteger(Semantic) != true || Semantic < 0 || Semantic >= LibraryVersion_Split.length) {
				console.warn("%cSimplify JavaScript - Version", "font-weight: bold;", "\n", "Unknown version semantic \"" + JSON.stringify(Semantic) + "\! Version semantic must be type of number, integer, between 0 and " + LibraryVersion_Split.length + "! Returned full detail as a result.");
				return LibraryVersion;
			} else {
				return LibraryVersion_Split[Semantic];
			};
		};
	/* Test */
		function Test(String = "Hello, developer!") {
			return console.log(String);
		};

/* Library Module */
	/* Virtual Storage */
		const VirtualStorage = require("./virtualstorage.js");
	/* Browser */
		/* Information */
			const BrowserInformation = require("./browser/information.js");
		/* Storage */
			const BrowserCookie = require("./browser/cookie.js");
			const BrowserStorage = require("./browser/storage.js");
//			const BrowserDatabase = require("./browser/indexeddatabase.js");
//		/* Web Request */
//			const BrowserWebRequest = require("./browser/webrequest.js");
//		/* Internationalization */
//			const BrowserInternationalization = require("./browser/internationalization.js");
	/* Converter */
		const Converter = require("./converter.js");
//	/* Pattern */
//		const Pattern = require("./pattern.js");

/* Package Library */
	const SimplifyJavaScript = {
		Version: Version,
		V: Version,
		Test: Test,
		VirtualStorage: VirtualStorage,
		VStorage: VirtualStorage,
		Browser: {
			Information: BrowserInformation,
			Info: BrowserInformation,
			I: BrowserInformation,
			Cookie: BrowserCookie,
			Storage: BrowserStorage//,
//			Database: BrowserDatabase,
//			WebRequest: BrowserWebRequest,
//			Internationalization: BrowserInternationalization,
//			I18n: BrowserInternationalization,
//			Localization: BrowserInternationalization
		},
		Converter: Converter//,
//		Pattern: Pattern
	};
	const SJS = SimplifyJavaScript;
	export {
		SimplifyJavaScript,
		SJS
	};
