/* Factorial */
	Number.prototype.factorial = function() {
		return Math.factorial(this.valueOf());
	};
	Number.prototype.factor = Number.prototype.factorial;
	Number.prototype.fact = Number.prototype.factorial;

/* Permutation */
	Number.prototype.cumulativepermutation = function() {
		return Math.cumulativepermutation(this.valueOf());
	};
	Number.prototype.cumpermut = Number.prototype.cumulativepermutation;

/* Combination */
	Number.prototype.cumulativecombination = function() {
		return Math.cumulativecombination(this.valueOf());
	};
	Number.prototype.cumcombin = Number.prototype.cumulativecombination;
